@charset "utf-8";

/* CSS Document */

/*for all*/
html{font-family:sans-serif;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%;font-size:40px}
div,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
label,
input,
select,
textarea,
p,
th,
td{font-size:14px;}
body{font-size:1.4rem}
*{margin:0px;padding:0px;word-wrap:break-word;word-break:normal;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;line-height:150%;font-family:"microsoft yahei",Verdana,Arial,Helvetica,sans-serif;}
body{background:#f8f8f8;color:#333;background-repeat:no-repeat;background-position:center top;position:relative}
body,
html{height:100%}
li{list-style:none;}
img{border:0;vertical-align:middle;max-width:100%;}
td,
th{line-height:150%;color:#333;padding:3px;vertical-align:top!important;}
input,
select{border:0;background-image:none;font-family:"microsoft yahei",Verdana,Arial,Helvetica,sans-serif;-webkit-appearance:none}
input[type="button"]{-webkit-appearance:none}
select{border:1px solid #dedede;padding:2px}

/*input,input:focus,*/
button,
button:focus{-webkit-appearance:none;outline:none /*去除input默认样式*/}
input[type="submit"],
input[type="reset"],
input[type="button"],
select{-moz-appearance:none;appearance:none;-webkit-appearance:none;border-radius:0;background-image:none}
input[type=checkbox],
input[type=radio]{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:22px;height:22px;margin:0;cursor:pointer;vertical-align:bottom;background:url(../images/button-icon08.png) no-repeat;position:relative;-webkit-background-size:100% 100%;background-size:100%;outline:none;margin-right:5px}
input[type=checkbox]:checked,
input[type=checkbox].active,
input[type=radio]:checked,
input[type=radio].active{background:url(../images/button-icon08.png) no-repeat 0 bottom;-webkit-background-size:100% 100%;background-size:100%;outline:none;}
.jc-menu input[type="button"]{border-radius:5px;}
a{color:#333;text-decoration:none;cursor:pointer;}
a:hover{text-decoration:none;}
table{word-break:break-all;}
table a{color:#135589;}
table a:hover{color:#f1524f;}
.clear{background:none;border:0;clear:both;display:block;float:none;font-size:0;margin:0;padding:0;overflow:hidden;visibility:hidden;width:0;height:0;}
.clearfix:after{visibility:hidden;display:block;content:" ";clear:both;height:0;line-height:0;font-size:0;}
.clearfix{*zoom:1;}
.yelow{color:#ffda44;}
.pd-t46{padding-top:45px;}
.pd-t20{padding-top:20px;}
.pd-bg{background-color:#cee4f6;height:100%;}
input,
select,
textarea{color:#000;font:normal 12px/auto;font-family:"microsoft yahei",Verdana,Arial,Helvetica,sans-serif;}
input,
select{vertical-align:middle;}
textarea{word-wrap:break-word;word-break:normal;}
.hide{display:none!important;visibility:hidden!important}
.affix{position:fixed!important;z-index:9996;}
.pull-left{float:left}
.pull-right{float:right}
.font-gray{color:#999!important}
.font-rose{color:#dc4747!important;}
.font-red{color:#e62c2c}
.font-orenge{color:#fd9c01;}
.white-bg{background-color:#fff;}
.gray-hs{background-color:#eee;}
.font-default{color:#ff4444;}
.box10{padding:10px 10px 0 10px;}
.box10 p{border-top:1px solid #eee;padding-top:10px;}
