/*btn*/
.sub-bnt{padding:25px 5px 30px 10px;text-align:center;display:table;width:100%;}
.btn{height:35px;display:inline-block;text-align:center;padding:0;line-height:35px;font-size:16px;border-radius:4px!important;}
button.btn{border:none;}

/*a.btn {border: 1px solid #999}*/

.btn-yelow{background-color:#fff;color:#eca847;border:1px solid #eca847;}
.btn-yelow.btn-block{color:#eca847;}
.btn-yelow:active{ background: #eca847;color: #fff;}
.btn-red{background-color:#ff4444;}
.btn-red.btn-block{color:#fff;}
.btn-block{width:100%;}
.btn-2x{width:48%}
