/* modal */
.close,
.close span{position:absolute;font-size:24px;line-height:1;color:#000;text-shadow:0 1px 0 #fff;filter:alpha(opacity=80);opacity:.8;top:3px!important;right:3px!important;}
.close:hover,
.close:focus{color:#000;text-decoration:none;cursor:pointer;filter:alpha(opacity=90);opacity:.9;}
button.close{-webkit-appearance:none;padding:0;cursor:pointer;background:transparent;border:0;}
.sr-only{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0px,0px,0px,0px);border:0;}
.modal-open{overflow:hidden;}
.modal{position:fixed;top:0;right:0;bottom:0;left:0;z-index:9999;display:none;overflow:hidden;-webkit-overflow-scrolling:touch;outline:0;}
.modal.fade .modal-dialog{-webkit-transition:-webkit-transform .3s ease-out;-o-transition:-o-transform .3s ease-out;transition:-webkit-transform .3s ease-out;transition:transform .3s ease-out;transition:transform .3s ease-out, -webkit-transform .3s ease-out;-webkit-transform:translate(0,-25%);-ms-transform:translate(0,-25%);-o-transform:translate(0,-25%);transform:translate(0,-25%);}
.modal.in .modal-dialog{-webkit-transform:translate(0,0);-ms-transform:translate(0,0);-o-transform:translate(0,0);transform:translate(0,0);}
.modal-open .modal{overflow-x:hidden;overflow-y:auto;}
.modal-dialog{position:relative;width:auto;margin:10px;z-index:7}
.modal-content{position:relative;background-color:#fff;-webkit-background-clip:padding-box;background-clip:padding-box;outline:0;-webkit-box-shadow:0 3px 9px rgba(0,0,0,.5);box-shadow:0 3px 9px rgba(0,0,0,.5);border-radius:10px;}
.modal-backdrop,
.loading-box{position:fixed;top:0;right:0;bottom:0;left:0;background-color:#000;}
.modal-backdrop{z-index:6}
.loading-box{background-color:#fff;width:100%;height:100%;background-image:url(../images/loading3.gif);background-position:center;background-repeat:no-repeat;z-index:10000}
.loading-box2{background-color:#fff;width:100%;height:100%;background-image:url(../images/loading2.gif);background-position:center;background-repeat:no-repeat;z-index:10000}
.loading-box.absolute{position:absolute}
.modal-backdrop.fade,
.loading-box.fade{filter:alpha(opacity=1);opacity:1;}
.modal-backdrop.in,
.loading-box.in{filter:alpha(opacity=50);opacity:.5;}
.modal-header{min-height:16.42857143px;padding:10px;background-color:#e40177;border-radius:10px 10px 0 0;}
.modal.modal-prompt .modal-header{background-color:#ffda44;}
.modal-header .close,
.modal-header .close span{margin-top:-2px;color:#333;}
.modal-title{margin:0;line-height:1.42857143;color:#333;font-size:16px;}
.modal-body{position:relative;padding:10px;}
.modal-footer{padding:10px;text-align:center;border-top:1px solid #e5e5e5;}
.modal-footer .btn{font-size:14px;color:#333;}
.modal-footer .btn + .btn{margin-bottom:0;margin-left:5px;display:inline-block}
.modal-footer .btn-group .btn + .btn{margin-left:-1px;}
.modal-footer .btn-block + .btn-block{margin-left:0;}
.modal-scrollbar-measure{position:absolute;top:-9999px;width:50px;height:50px;overflow:scroll;}
.yc-fr{background-color:#fff;overflow:hidden;line-height:10px;text-align:center;}
.yc-fr .fr-cl{display:block;padding-bottom:15px;}
.modal-radius .modal-dialog{width:100%;height:100%}
.modal-radius .modal-content{border-radius:4px;-moz-border-radius:4px;-webkit-border-radius:4px;overflow:hidden;-webkit-box-shadow:none;box-shadow:none;border:0;width:280px;left:50%;margin-left:-140px;top:50%;margin-top:-100px}
.modal-radius .modal-body{height:100px;display:table;vertical-align:middle;width:100%}
.modal-radius .popup-text{height:100%;display:table-cell;vertical-align:middle}

/*合约弹出层样式*/
.modal-hy .modal-dialog{margin:0;height:100%}
.modal-hy .modal-content{-webkit-box-shadow:none;box-shadow:none;border-width:0;height:100%;background-color:#fff;padding-bottom:50px}
.modal-hy .hy-content{height:100%;overflow-y:auto;}
